import * as R from "ramda";

import extractLink from "./extractLink.js";
import extractRichText from "./extractRichText.jsx";
import extractState from "./extractState.js";

const extractCampaign = R.compose(
  R.applySpec({
    id: R.prop("id"),
    campaignName: R.prop("campaignName"),
    campaignDescription: R.compose(
      R.unless(R.isNil, extractRichText),
      R.prop("campaignDescription"),
    ),
    city: R.prop("city"),
    state: R.prop("state"),
    leadOrganization: R.prop("leadOrganization"),
    email: R.prop("email"),
    phone: R.prop("phone"),
    linkText: R.prop("linkText"),
    linkUrl: R.compose(extractLink, R.prop("linkUrl")),
    taxonomy: R.compose(extractState, R.prop("taxonomy")),
  }),
  R.defaultTo({}),
);

export default extractCampaign;
