import "./us-map.scss";

import * as R from "ramda";

import { func, number, string } from "prop-types";
import { geoAlbersUsa, geoPath } from "d3-geo";
import { select, selectAll } from "d3-selection";
import { useEffect, useMemo, useRef } from "react";

import Select from "../Select/Select.jsx";
import cx from "classnames";
import { feature } from "topojson";
import us from "./states-10m.json"; // https://github.com/topojson/us-atlas

const propTypes = {
  width: number,
  height: number,
  selectedState: string,
  handleSelect: func,
};

const defaultProps = {
  width: 1720,
  height: 1206,
};

const UsMap = (props) => {
  const { width, height, selectedState, handleSelect } = props;

  const svgWidth = width;
  const svgHeight = height;

  const containerRef = useRef();

  const geoJson = useMemo(() => {
    return feature(us, us.objects.states);
  }, []);

  const projection = useMemo(() => {
    return geoAlbersUsa().fitSize([svgWidth, svgHeight], geoJson);
  }, [geoJson, svgHeight, svgWidth]);

  const generatePath = useMemo(() => {
    return geoPath().projection(projection);
  }, [projection]);

  const getBubblePos = () => {
    const w = 320;
    const startX = width - w - 22;
    const startY = 40;
    return `M${startX},${startY} h${w} a20,20 0 0 1 20,20 v50 a20,20 0 0 1 -20,20 h-${w} a20,20 0 0 1 -20,-20 v-50 a20,20 0 0 1 20,-20 z`;
  };

  const getBubbleTextPos = () => {
    const x = width - 342;
    const y = 97;
    return {
      x: x,
      y: y,
    };
  };

  const states = R.compose(
    (data) => ["National", ...data],
    R.sortBy(R.identity),
    R.map(R.path(["properties", "name"])),
  )(geoJson.features);

  useEffect(() => {
    // Add / remove tooltip on hover
    selectAll(".us-map__state")
      .on("mouseenter", (e) => {

        const name = R.path(["target", "dataset", "stateName"])(e);

        if(name === "DC_bubble") {
          select("svg path[data-state-abbreviation='DC']")
            .attr("class", "us-map__state us-map__state__selected");
        } else {
          // Define the div for the tooltip
          const div = select(".us-map__container")
            .append("div")
            .attr("class", "tooltip")
            .style("opacity", 0);

          const elemBounds = e.target.getBoundingClientRect();
          const containerBounds = containerRef.current.getBoundingClientRect();

          const scale = () => {
            return elemBounds.height < 30 ? -10 : 20;
          };

          const posY =
            elemBounds.height < 50
              ? elemBounds.y - containerBounds.top - elemBounds.height + scale()
              : elemBounds.y - containerBounds.top - 5;
          const posX = elemBounds.x - 20 + elemBounds.width / 2;

          const value = R.path(["target", "dataset", "stateAbbreviation"])(e);

          div.style("opacity", 1);
          div.html(value).style("left", `${posX}px`).style("top", `${posY}px`);
        }
      })
      .on("mouseout", function (d) {
        select(".us-map__container .tooltip").remove();
      });
  }, []);


  // Prevents bordering states overlapping selected state
  const sortedStates = R.compose(
    (arr) =>
      R.concat(
        arr,
        R.filter((feature) => {
          const name = R.path(["properties", "name"], feature);
          return name === selectedState;
        }, geoJson.features),
      ),
    R.filter((feature) => {
      const name = R.path(["properties", "name"], feature);
      return name !== selectedState;
    }),
  )(geoJson.features);

  return (
    <div className="us-map">
      <Select
        selectProps={{
          items: states,
          itemToString: (d) => d,
          selectedItem: selectedState,
        }}
        onChange={handleSelect}
        placeholder="Select a state"
        style={{ width: 370, height: 56 }}
      />
      <div ref={containerRef} className="us-map__container">
        <svg
          className="us-map__svg"
          width={svgWidth}
          height={svgHeight}
          viewBox={`0 0 ${svgWidth} ${svgHeight}`}
        >
          <g className="us-map__states">
            {sortedStates.map((feature) => {
              const name = R.path(["properties", "name"], feature);
              const abbreviation = R.path(["properties", "abbr"], feature);
              if (name !== "District of Columbia") {
                return (
                  <path
                    className={cx("us-map__state", {
                      "us-map__state--selected": selectedState === name,
                    })}
                    data-state-name={name} // for easy identification in dev tools
                    data-state-abbreviation={abbreviation}
                    key={feature.id} // FIPS id
                    fill="transparent"
                    onClick={() => handleSelect(name)}
                    d={generatePath(feature)}
                  />
                );
              } else {
                return (
                  <g>
                    <path
                      className={cx("us-map__state", {
                        "us-map__state--selected": selectedState === name,
                      })}
                      data-state-name={name} // for easy identification in dev tools
                      data-state-abbreviation={abbreviation}
                      key={feature.id} // FIPS id
                      fill="transparent"
                      onClick={() => handleSelect(name)}
                      d={generatePath(feature)}
                    />
                    {/* <path
                      className={cx("us-map__state", {
                        "us-map__state--selected": selectedState === name,
                      })}
                      data-state-name={abbreviation + '_bubble'} // for easy identification in dev tools
                      key={feature.id + "_bubble"}
                      fill="transparent"
                      d={getBubblePos()}
                      onClick={() => handleSelect(name)}
                    />
                    <text
                      {...getBubbleTextPos()}
                      font-size="32"
                      fill="white"
                      key={feature.id + "_text"}
                    >
                      District of Columbia
                    </text> */}
                  </g>
                );
              }
            })}
          </g>
        </svg>

        <button
          className={cx("us-map__btn", "button", {
            "us-map__state--selected": selectedState === "District of Columbia",
          })}
          key={feature.id + "_bubble"}
          onClick={() => handleSelect("District of Columbia")}
        >
          <p>District of Columbia</p>
        </button>
      </div>
    </div>
  );
};

UsMap.propTypes = propTypes;
UsMap.defaultProps = defaultProps;
export default UsMap;
