import "./state-info.scss";

import * as R from "ramda";

import ContentfulLink from "../ContentfulLink/ContentfulLink.jsx";
import { object } from "prop-types";

const propTypes = {
  source: object,
  contentfulStateData: object,
  data: object,
};

const StateInfo = (props) => {
  const { source, contentfulStateData, data } = props;

  const extractSource = (key) => {
    const label = R.path(["sourceLabel", key], source);
    const path = R.path(["sourceUrl", key], source);

    const infoSvg = (
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
        <path
          fill="#fff"
          d="M12 2C6.477 2 2 6.477 2 12s4.477 10 10 10 10-4.477 10-10S17.523 2 12 2zm1 15h-2v-6h2zm0-8h-2V7h2zm0 0"
        />
      </svg>
    );

    return label && path ? (
      <p className="txt--small state-data__source">
        {infoSvg} <span>Source:</span>
        <ContentfulLink type="external" to={path}>
          {label}
        </ContentfulLink>
      </p>
    ) : null;
  };

  // Filter out null data
  const checkValid = (val) => {
    const formatted = val.replace(/[^\d.]/g, "");
    return !isNaN(formatted) && formatted !== "";
  };

  return (
    <div className="state-info">
      <div className="state-info__section">
        <h3>
          <strong>DATA</strong>
        </h3>
        {!R.isEmpty(contentfulStateData) && (
          <p>{contentfulStateData.description}</p>
        )}
      </div>

      {data && checkValid(data.PRISON_INCARCERATION_RATE_2019) && (
        <div className="state-info__section">
          <h4>
            <strong>{data.PRISON_INCARCERATION_RATE_2019}</strong>
          </h4>
          <p>Prison incarceration rate 2019</p>
          <p className="txt--small">(Per 100,000 adults)</p>
          {extractSource("PRISON_INCARCERATION_RATE_2019")}
        </div>
      )}

      {data && checkValid(data.JAIL_INCARCERATION_RATE_2015) && (
        <div className="state-info__section">
          <h4>
            <strong>{data.JAIL_INCARCERATION_RATE_2015}</strong>
          </h4>
          <p>Jail incarceration rate 2015</p>
          <p className="txt--small">(Per 100,000 adults)</p>
          {extractSource("JAIL_INCARCERATION_RATE_2015")}
        </div>
      )}

      {data && checkValid(data.PRETRIAL_INCARCERATION_RATE_2015) && (
        <div className="state-info__section">
          <h4>
            <strong>{data.PRETRIAL_INCARCERATION_RATE_2015}</strong>
          </h4>
          <p>Pretrial incarceration rate 2015</p>
          <p className="txt--small">(Per 100,000 adults)</p>
          {extractSource("PRETRIAL_INCARCERATION_RATE_2015")}
        </div>
      )}

      {data && checkValid(data.JUVENILE_CUSTODY_RATE_2015) && (
        <div className="state-info__section">
          <h4>
            <strong>{data.JUVENILE_CUSTODY_RATE_2015}</strong>
          </h4>
          <p>Juvenile incarceration rate 2015</p>
          <p className="txt--small">(Per 100,000 adults)</p>
          {extractSource("JUVENILE_CUSTODY_RATE_2015")}
        </div>
      )}

      {data && checkValid(data.DISENFRANCHISED_AFRICAN_AMERICANS_2020) && (
        <div className="state-info__section">
          <h4>
            <strong>{data.DISENFRANCHISED_AFRICAN_AMERICANS_2020}</strong>
          </h4>
          <p>Disenfranchised African Americans 2020</p>
          <p className="txt--small">(% Disenfranchised)</p>
          {extractSource("DISENFRANCHISED_AFRICAN_AMERICANS_2020")}
        </div>
      )}

      {data && checkValid(data.TOTAL_CORRECTION_EXPENDITURES) && (
        <div className="state-info__section">
          <h4>
            <strong>{data.TOTAL_CORRECTION_EXPENDITURES} million</strong>
          </h4>
          <p>Total expenditures on corrections</p>
          {extractSource("TOTAL_CORRECTION_EXPENDITURES")}
        </div>
      )}
    </div>
  );
};

StateInfo.propTypes = propTypes;

export default StateInfo;
