import "./campaign.scss";

import * as R from "ramda";

import { graphql, useStaticQuery } from "gatsby";
import { useEffect, useState } from "react";

import Button from "../Button/Button.jsx";
import CampaignCard from "./CampaignCard.jsx";
import FlipMove from "react-flip-move";
import cx from "classnames";
import extractCampaign from "../../common/extractCampaign.js";
import { string } from "prop-types";
import useAOS from "../../common/useAOS.js";
import useMatchMedia from "../../common/useMatchMedia.js";

const propTypes = {
  state: string.isRequired,
};

const Campaign = (props) => {
  const { state } = props;
  const campaignData = useStaticQuery(query);

  useAOS();

  const [campaigns, setCampaigns] = useState([]);
  const [campaignsToShow, setCampaignsToShow] = useState(3);
  const isDesktop = useMatchMedia("(min-width: 910px)"); // matches $bp-large-min in _breakpoints.scss

  useEffect(() => {
    const data = R.compose(
      R.map(R.unless(R.isNil, extractCampaign)),
      (nodes) => {
        return nodes.filter((node) => {
          if (node.taxonomy) {
            const taxonomy = node.taxonomy.find(
              (taxonomyItem) => taxonomyItem.state === state,
            );

            return taxonomy ? true : false;
          }
          return false;
        });
      },
      R.path(["allContentfulCampaign", "nodes"]),
    )(campaignData);

    setCampaignsToShow(3);
    setCampaigns(data);
  }, [campaignData, state]);

  const loadMore = () => {
    setCampaignsToShow(campaigns.length);
  };

  const showLoadMore =
    campaigns.length > campaignsToShow && campaignsToShow !== campaigns.length;

  return (
    <div className="campaigns">
      <h3>
        <strong>CAMPAIGNS</strong>
      </h3>
      {state === "National" ? (
        <p>
          We found <strong>{campaigns.length}</strong> opportunities for you to
          get involved and advocate for change. If you’d like to add your
          campaign to the map, please{" "}
          <a href="mailto:JusticeProgram@civilrights.org">contact us</a>!
          Together, let’s reimagine what safety means.
        </p>
      ) : campaigns.length ? (
        <p>
          We found <strong>{campaigns.length}</strong> opportunities for you to
          get involved and advocate for change in your state. If you’d like to
          add your campaign to the map, please{" "}
          <a href="mailto:JusticeProgram@civilrights.org">contact us</a>!
          Together, let’s reimagine what safety means.{" "}
        </p>
      ) : (
        <p>
          It looks like we haven’t yet identified any opportunities to advocate
          for change in your state. Will you help us build our map? If you’d
          like to add your campaign,{" "}
          <a href="mailto:JusticeProgram@civilrights.org">contact us</a>!
          Together, let’s reimagine what safety means.
        </p>
      )}

      {!R.isEmpty(campaigns) && (
        <FlipMove
          className={cx("campaign__cards", {
            "campaign__cards--2-up": isDesktop && campaignsToShow >= 5,
          })}
        >
          {campaigns.slice(0, campaignsToShow).map((campaign) => (
            <CampaignCard
              data-aos="fade-down"
              key={campaign.id}
              campaign={campaign}
            />
          ))}
        </FlipMove>
      )}
      {showLoadMore && (
        <Button className="btn--big" onClick={loadMore}>
          Load more
        </Button>
      )}
    </div>
  );
};

Campaign.propTypes = propTypes;

export default Campaign;

const query = graphql`
  {
    allContentfulCampaign {
      nodes {
        ...Campaign
      }
    }
  }
`;
