import {} from "prop-types";
import "./map-page.scss";

import * as R from "ramda";

import { graphql, useStaticQuery, withPrefix } from "gatsby";
import { useEffect, useRef, useState } from "react";

import BreadCrumb from "../../components/BreadCrumb/BreadCrumb.jsx";
import Campaign from "../Campaign/Campaign";
import Container100 from "../Container/Container100.jsx";
import Container6040 from "../Container/Container6040.jsx";
import ContainerBleed from "../Container/ContainerBleed.jsx";
import Page from "../Page/Page.jsx";
import PageTitle from "../../components/PageTitle/PageTitle.jsx";
import SignUp from "../SignUp/SignUp.jsx";
import StateData from "./StateInfo.jsx";
import TwoUpTextBanner from "../TwoUpTextBanner/TwoUpTextBanner.jsx";
import UsMap from "../UsMap/UsMap.jsx";
import extractPageTitle from "../../common/extractPageTitle.js";
import extractTwoUpTextBanner from "../../common/extractTwoUpTextBanner.js";
import { scrollToElement } from "../../common/scrollHelpers";
import useFetchData from "../../common/useFetchData.js";
import useScrollama from "../../common/useScrollama.js";

const propTypes = {};

const MapPage = (props) => {
  const contentfulData = useStaticQuery(query);

  const [hasMounted, setHasMounted] = useState(false);

  const [selectedState, setSelectedState] = useState("National");
  const [stateData, setStateData] = useState({});
  const [gradientStop, setGradientStop] = useState(0);

  useEffect(() => {
    setHasMounted(true);
  }, []);

  const data = R.compose(
    R.evolve({
      mapPageTitle: R.unless(R.isNil, extractPageTitle),
      mapPageTwoUpTextBanner: R.unless(R.isNil, extractTwoUpTextBanner),
    }),
    R.defaultTo({}),
    R.prop("contentfulMapPage"),
  )(contentfulData);

  const { id, mapPageTitle, mapPageTwoUpTextBanner } = data;

  // Pull in supplemental fields from Contentful
  const contentfulStateData = R.compose(
    R.defaultTo([]),
    R.path(["allContentfulState", "nodes"]),
  )(contentfulData);

  // JSON gets built inside gatsby-node.js
  const dataFetch = useFetchData(withPrefix("map-data.json"));

  const source = {
    sourceLabel: stateData ? R.prop("Source", stateData) : "",
    sourceUrl: stateData ? R.prop("Notes", stateData) : "",
  };

  useEffect(() => {
    if (!R.isEmpty(dataFetch.data) || !R.isNil(dataFetch.data))
      setStateData(dataFetch.data);
  }, [dataFetch]);

  useEffect(() => {
    if (selectedState !== null && selectedState !== "National") {
      scrollToElement(colorStop2Ref.current);
    }
  }, [selectedState]);

  const colorStop1Ref = useRef();
  const colorStop2Ref = useRef();
  const colorStop3Ref = useRef();

  /* Scrollama Effects */
  useScrollama(colorStop1Ref, {
    offset: 0,
    // debug: true,
    onStepEnter: (response) => setGradientStop(0),
  });

  useScrollama(colorStop2Ref, {
    offset: 0.5,
    // debug: true,
    onStepEnter: (response) => setGradientStop(1),
  });

  useScrollama(colorStop3Ref, {
    offset: 0,
    // debug: true,
    onStepEnter: (response) => setGradientStop(2),
  });

  return (
    <Page
      pageTitle="Find a Campaign"
      pageDescription={mapPageTitle && mapPageTitle?.description}
      className="map-page"
      gradient={gradientStop}
    >
      <BreadCrumb pageId={id}>breadcrumb</BreadCrumb>
      <ContainerBleed>
        <Container100>
          <div ref={colorStop1Ref}>
            <PageTitle {...mapPageTitle} />
          </div>
        </Container100>
        <div className="map-page__map-section">
          {hasMounted && (
            <UsMap
              selectedState={selectedState}
              handleSelect={setSelectedState}
            />
          )}
        </div>

        <div className="map-page__state-section" ref={colorStop2Ref}>
          {selectedState && (
            <Container100>
              <div className="map-page__state-heading">
                <h2>
                  <strong>{selectedState}</strong>
                </h2>
                <h3>
                  There are many ways to make a difference in your community.
                </h3>
              </div>
              {selectedState === "National" ? (
                <Container100 className="map-page__state-content">
                  <Campaign state={selectedState} />
                </Container100>
              ) : (
                <Container6040
                  className="map-page__state-content"
                  left={<Campaign state={selectedState} />}
                  right={
                    <StateData
                      source={source}
                      contentfulStateData={
                        R.find(R.propEq("state", selectedState))(
                          contentfulStateData,
                        ) || {}
                      }
                      data={stateData[selectedState]}
                    />
                  }
                />
              )}
            </Container100>
          )}
        </div>
      </ContainerBleed>
      <ContainerBleed>
        <div ref={colorStop3Ref} className="map-page__state-section">
          <TwoUpTextBanner {...mapPageTwoUpTextBanner} />
          <SignUp
            title="Join Our Movement"
            subtitle="Sign up for our emails and learn how to get involved in Vision for Justice and help transform our criminal-legal system."
          />
        </div>
      </ContainerBleed>
    </Page>
  );
};

MapPage.propTypes = propTypes;

export default MapPage;

const query = graphql`
  {
    contentfulMapPage(id: { eq: "a9a10325-390a-5d55-bcad-6ce6899e0098" }) {
      id
      mapPageTitle {
        pageHeadline
        pageDescription
      }
      mapPageTwoUpTextBanner {
        ...TwoUpTextBanner
      }
    }
    allContentfulState {
      nodes {
        state
        description
      }
    }
  }
`;
