import "./campaign-card.scss";

import * as R from "ramda";

import ContentfulLink from "../ContentfulLink/ContentfulLink";
import campaignShape from "./campaignShape.js";
import { forwardRef } from "react";
import { navigate } from "gatsby";
import { useContentfulSiteMap } from "../../common/contentfulSiteMap.jsx";

const propTypes = {
  campaign: campaignShape,
};

// forwardRef required for FlipMove animation to work
const CampaignCard = forwardRef((props, ref) => {
  const { campaign } = props;

  const siteMap = useContentfulSiteMap();

  const {
    city,
    state,
    campaignName,
    campaignDescription,
    leadOrganization,
    phone,
    email,
    linkUrl,
    linkText,
  } = campaign;

  const handleClick = (link) => {
    const path = link.to || R.path([link.pageId, "path"])(siteMap);

    if (!R.isNil(path)) {
      // for external and media links, open a in a new window
      if (link.type === "external") {
        window.open(path, "_blank");
      }
      // for internal/page links, navigate regularly
      else {
        navigate(path);
      }
    }
  };

  return (
    <div
      ref={ref}
      role="presentation"
      tabIndex="-1"
      onClick={() => handleClick(linkUrl)}
      className="campaign-card"
    >
      {(city || state) && (
        <p className="campaign-card__eyebrow">
          <strong>{city ? `${city}, ${state}` : state}</strong>
        </p>
      )}
      {campaignName && (
        <h4>
          <strong>{campaignName}</strong>
        </h4>
      )}
      {campaignDescription && campaignDescription}
      {(phone || email || leadOrganization) && (
        <div className="campaign-card__contact-info">
          {leadOrganization && (
            <p>
              <strong>Lead Organization(s)</strong> {leadOrganization}
            </p>
          )}
          {phone && (
            <p>
              <strong>Phone</strong> {phone}
            </p>
          )}
          {email && (
            <p>
              <strong>Email</strong> {email}
            </p>
          )}
        </div>
      )}
      {linkUrl && linkText && (
        <ContentfulLink className="campaign-card__link" {...linkUrl}>
          {linkText}
        </ContentfulLink>
      )}
    </div>
  );
});

CampaignCard.propTypes = propTypes;

export default CampaignCard;
